@import '../../variables.scss';

.cartRightWrapper {
  // width: 20%;
  height: 100%;
  position: fixed;
  top: 0;
  // right: -20%;
  border-left: $mainBorderWidth * 2 solid $mainBorderColor;
  background-color: $white;
  z-index: 1;
  display: flex;
  flex-direction: column;
  padding: $mainPadding * 2;
  .cartRight {
    .top {
      width: 100%;
      display: flex;
      padding: $mainPadding $mainPadding $mainPadding * 4 $mainPadding;
      .totalPrice {
        width: 50%;
        font-size: $mainFontSize;
      }
      .closeCart {
        width: 50%;
        text-align: end;
        i {
          font-size: $mainFontSize * 2;
          color: $mainColor;
          &:hover {
            color: $mainHoverColor;
          }
        }
      }
    }
    .cartItems {
      // scss variable not working in calc
      height: calc(100vh - 8px * 19);
      overflow: auto;
      padding: 0 $mainPadding $mainPadding $mainPadding;
    }
    .order {
      text-align: center;
      padding: $mainPadding;
      > div {
        display: inline-block;
        padding: $mainPadding / 2 $mainPadding;
        border: $mainBorderWidth * 2 solid $mainColor;
        border-radius: $mainBorderRadius;
        cursor: pointer;
        transition: $mainTransition;
        background-color: white;
        &:hover {
          color: $white;
          background-color: $mainColor;
        }
      }
    }
  }
}
